@use 'base/variables' as v;
@use 'base/mixins' as m;

.resumen {
    
    background: linear-gradient(to right, rgba(v.$azul, .7), rgba(v.$azul, .9)), url('../img/bg_numeros.jpg');
    background: linear-gradient(to right, rgba(v.$azul, .7), rgba(v.$azul, .9)), image-set(url('../img/bg_numeros.avif') 1x, url('../img/bg_numeros.webp') 1x, url('/build/img/bg_numeros.jpg') 1x);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 10rem 0;
    
    &__grid {
        
        @include m.contenedor;
        @include m.grid(1,5rem);
        @include m.tablet {
            @include m.grid(2,5rem);
        }
        
    }
    
    &__bloque {
        
        text-align: center;
        color: v.$blanco;
        
    }
    
    &__texto {
        
        font-weight: v.$black;
        line-height: 1;
        margin: 0;
        font-size: 2.5rem;
        text-transform: uppercase;
        
        &--numero {
            
            @extend .resumen__texto;
            font-size: 8rem;
            
        }
        
    }
}